import { useState } from 'react';

/**
 * 
 */
const keySizes = {
    rsa: [
        {
            value: '2048',
            label: '2048',
            description: '2030',
            details: {
                security: '112',
                speed: '',
                compatibility: '',
                recommendation: 'Web'
            }
        },
        {
            value: '3072',
            label: '3072',
            description: '2040',
            details: {
                security: '128',
                speed: '20481.5',
                compatibility: '',
                recommendation: ''
            }
        },
        {
            value: '4096',
            label: '4096',
            description: '',
            details: {
                security: '144',
                speed: '20482',
                compatibility: '',
                recommendation: ''
            }
        }
    ],
    ecdsa: [
        {
            value: 'P-256',
            label: 'P-256',
            description: '',
            details: {
                security: '128',
                speed: 'RSA 2048',
                compatibility: 'TLS 1.3',
                recommendation: 'Web'
            }
        },
        {
            value: 'P-384',
            label: 'P-384',
            description: '',
            details: {
                security: '192',
                speed: 'P-2561.5',
                compatibility: 'NSA Suite B',
                recommendation: ''
            }
        }
    ],
    eddsa: [
        {
            value: 'Ed25519',
            label: 'Ed25519',
            description: 'SSH/',
            details: {
                security: '128',
                speed: 'ECDSA',
                compatibility: 'OpenSSH 6.5+',
                recommendation: 'SSH'
            }
        },
        {
            value: 'Ed448',
            label: 'Ed448',
            description: '',
            details: {
                security: '224',
                speed: 'Ed255192',
                compatibility: 'OpenSSL 1.1.1+',
                recommendation: ''
            }
        }
    ]
};

/**
 * Step2
 * @param {Object} props - 
 * @param {string} props.keyType - 
 * @param {function} props.onSelect - 
 * @param {string} props.selected - 
 * @param {function} props.onBack - 
 * @param {function} props.onNext - 
 * @param {string} props.language - 
 */
export default function Step2({ keyType, onSelect, selected, onBack, onNext, language }) {
    const [showDetails, setShowDetails] = useState(null);

    // 
    const texts = {
        ja: {
            title: '',
            subtitle: '',
            details: '',
            security: '',
            speed: '',
            compatibility: '',
            recommendation: '',
            back: '',
            next: '',
            close: ''
        },
        en: {
            title: 'Select Key Size',
            subtitle: 'Choose the appropriate key size for your use case',
            details: 'Details',
            security: 'Security Level',
            speed: 'Processing Speed',
            compatibility: 'Compatibility',
            recommendation: 'Recommended Use',
            back: 'Back',
            next: 'Next',
            close: 'Close'
        }
    }[language];

    // 
    const availableSizes = keySizes[keyType] || [];

    return (
        <div className="space-y-6">
            <div className="text-center">
                <h2 className="text-2xl font-bold">{texts.title}</h2>
                <p className="text-gray-600 mt-2">{texts.subtitle}</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {availableSizes.map(size => (
                    <div key={size.value} className="relative">
                        <button
                            onClick={() => onSelect(size.value)}
                            onDoubleClick={() => {
                                if (selected) {
                                    onNext();
                                } else {
                                    alert(
                                        language === 'ja'
                                            ? ''
                                            : 'Please select a key size'
                                    );
                                }
                            }}
                            className={`w-full p-4 border rounded-lg hover:bg-blue-50 transition-colors ${
                                selected === size.value ? 'border-blue-500 bg-blue-50' : 'border-gray-200'
                            }`}
                        >
                            <div className="font-bold text-lg">{size.label}</div>
                            <div className="text-sm text-gray-600 mt-1">{size.description}</div>
                        </button>
                        <button
                            onClick={() => setShowDetails(size.value)}
                            className="absolute top-2 right-2 p-1 text-gray-500 hover:text-gray-700"
                            aria-label={`${texts.details}: ${size.label}`}
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                            </svg>
                        </button>
                    </div>
                ))}
            </div>

            <div className="flex justify-between">
                <button
                    onClick={onBack}
                    className="px-4 py-2 text-blue-600 hover:text-blue-800"
                >
                    {texts.back}
                </button>
                <button
                    onClick={() => {
                        if (selected) {
                            onNext();
                        } else {
                            alert(
                                language === 'ja'
                                    ? ''
                                    : 'Please select a key size'
                            );
                        }
                    }}
                    disabled={!selected}
                    className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-300"
                >
                    {texts.next}
                </button>
            </div>

            {/*  */}
            {showDetails && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
                    <div className="bg-white rounded-lg max-w-2xl w-full p-6">
                        <div className="flex justify-between items-start">
                            <h3 className="text-xl font-bold">
                                {availableSizes.find(s => s.value === showDetails).label}
                            </h3>
                            <button
                                onClick={() => setShowDetails(null)}
                                className="text-gray-500 hover:text-gray-700"
                            >
                                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <div className="mt-4 space-y-4">
                            <div>
                                <h4 className="font-bold">{texts.security}:</h4>
                                <p className="text-gray-600 mt-1">
                                    {availableSizes.find(s => s.value === showDetails).details.security}
                                </p>
                            </div>

                            <div>
                                <h4 className="font-bold">{texts.speed}:</h4>
                                <p className="text-gray-600 mt-1">
                                    {availableSizes.find(s => s.value === showDetails).details.speed}
                                </p>
                            </div>

                            <div>
                                <h4 className="font-bold">{texts.compatibility}:</h4>
                                <p className="text-gray-600 mt-1">
                                    {availableSizes.find(s => s.value === showDetails).details.compatibility}
                                </p>
                            </div>

                            <div>
                                <h4 className="font-bold">{texts.recommendation}:</h4>
                                <p className="text-gray-600 mt-1">
                                    {availableSizes.find(s => s.value === showDetails).details.recommendation}
                                </p>
                            </div>
                        </div>

                        <div className="mt-6">
                            <button
                                onClick={() => setShowDetails(null)}
                                className="w-full py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                            >
                                {texts.close}
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}