/**
 * 
 */
import { useState } from 'react';
import { validateOutputFormat } from '../utils/errorHandler';

/**
 * 
 */
const outputFormats = [
    {
        value: 'pem',
        label: 'PEM',
        description: 'PKCS#8/SPKIOpenSSL',
        details: {
            title: 'Privacy Enhanced Mail',
            description: `
                
                Base64ASN.1
                
            `,
            features: [
                'OpenSSL',
                '',
                '',
                ''
            ],
            useCases: [
                'TLS/SSL',
                'SSH',
                'S/MIME',
                '/'
            ]
        }
    },
    {
        value: 'jwk',
        label: 'JWK',
        description: 'JSON Web KeyWeb API',
        details: {
            title: 'JSON Web Key',
            description: `
                Web APIJWTJSON
                Web Crypto API
                Web
            `,
            features: [
                'JSON',
                'Web Crypto API',
                'JWT/JWS',
                'Base64URL'
            ],
            useCases: [
                'Web',
                'JWT/',
                '',
                'REST API'
            ]
        }
    },
    {
        value: 'ssh',
        label: 'SSH',
        description: 'OpenSSH',
        details: {
            title: 'OpenSSH',
            description: `
                SSH
                 authorized_keys 
                 id_rsa 
            `,
            features: [
                'OpenSSH',
                '',
                'SSH',
                'Base64'
            ],
            useCases: [
                'SSH',
                'GitHub',
                '',
                ''
            ]
        }
    },
    {
        value: 'pgp',
        label: 'OpenPGP',
        description: 'PGP/GPG',
        details: {
            title: 'OpenPGP',
            description: `
                
                GnuPGPGP
                
            `,
            features: [
                'GnuPG/PGP',
                'ASCII Armor',
                '',
                ''
            ],
            useCases: [
                '',
                '',
                '',
                ''
            ]
        }
    }
];

/**
 * Step3
 * @param {Object} props - 
 * @param {string} props.keyType - 
 * @param {string} props.keySize - 
 * @param {function} props.onSelect - 
 * @param {string} props.selected - 
 * @param {function} props.onBack - 
 * @param {string} props.language - 
 */
export default function Step3({ keyType, keySize, onSelect, selected, onBack, language }) {
    const [showDetails, setShowDetails] = useState(null);
    const [error, setError] = useState('');

    // 
    const texts = {
        ja: {
            title: '',
            subtitle: '',
            details: '',
            features: '',
            useCases: '',
            back: '',
            close: '',
            error: ''
        },
        en: {
            title: 'Select Output Format',
            subtitle: 'Choose the appropriate output format for your use case',
            details: 'Details',
            features: 'Features',
            useCases: 'Use Cases',
            back: 'Back',
            close: 'Close',
            error: 'This format is not available for the selected cryptography type'
        }
    }[language];

    // 
    const handleSelect = (format) => {
        const validation = validateOutputFormat({ keyType, keySize, outputFormat: format });
        if (!validation.isValid) {
            setError(validation.message);
            return;
        }
        setError('');
        onSelect(format);
    };

    return (
        <div className="space-y-6">
            <div className="text-center">
                <h2 className="text-2xl font-bold">{texts.title}</h2>
                <p className="text-gray-600 mt-2">{texts.subtitle}</p>
            </div>

            {error && (
                <div className="p-4 bg-red-50 text-red-700 rounded-lg">
                    {error}
                </div>
            )}

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {outputFormats.map(format => {
                    const validation = validateOutputFormat({
                        keyType,
                        keySize,
                        outputFormat: format.value
                    });
                    const isDisabled = !validation.isValid;

                    return (
                        <div key={format.value} className="relative">
                            <button
                                onClick={() => handleSelect(format.value)}
                                disabled={isDisabled}
                                className={`w-full p-4 border rounded-lg transition-colors ${
                                    isDisabled
                                        ? 'bg-gray-100 cursor-not-allowed opacity-60'
                                        : selected === format.value
                                        ? 'border-blue-500 bg-blue-50'
                                        : 'border-gray-200 hover:bg-blue-50'
                                }`}
                            >
                                <div className="font-bold text-lg">{format.label}</div>
                                <div className="text-sm text-gray-600 mt-1">{format.description}</div>
                            </button>
                            <button
                                onClick={() => setShowDetails(format.value)}
                                disabled={isDisabled}
                                className={`absolute top-2 right-2 p-1 ${
                                    isDisabled
                                        ? 'text-gray-400 cursor-not-allowed'
                                        : 'text-gray-500 hover:text-gray-700'
                                }`}
                                aria-label={`${texts.details}: ${format.label}`}
                            >
                                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                                </svg>
                            </button>
                        </div>
                    );
                })}
            </div>

            <div className="flex justify-start">
                <button
                    onClick={onBack}
                    className="px-4 py-2 text-blue-600 hover:text-blue-800"
                >
                    {texts.back}
                </button>
            </div>

            {/*  */}
            {showDetails && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
                    <div className="bg-white rounded-lg max-w-2xl w-full p-6">
                        <div className="flex justify-between items-start">
                            <h3 className="text-xl font-bold">
                                {outputFormats.find(f => f.value === showDetails).details.title}
                            </h3>
                            <button
                                onClick={() => setShowDetails(null)}
                                className="text-gray-500 hover:text-gray-700"
                            >
                                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <div className="mt-4 space-y-4">
                            <p className="text-gray-600">
                                {outputFormats.find(f => f.value === showDetails).details.description}
                            </p>

                            <div>
                                <h4 className="font-bold">{texts.features}:</h4>
                                <ul className="list-disc list-inside mt-2">
                                    {outputFormats
                                        .find(f => f.value === showDetails)
                                        .details.features.map((feature, index) => (
                                            <li key={index} className="text-gray-600">{feature}</li>
                                        ))}
                                </ul>
                            </div>

                            <div>
                                <h4 className="font-bold">{texts.useCases}:</h4>
                                <ul className="list-disc list-inside mt-2">
                                    {outputFormats
                                        .find(f => f.value === showDetails)
                                        .details.useCases.map((useCase, index) => (
                                            <li key={index} className="text-gray-600">{useCase}</li>
                                        ))}
                                </ul>
                            </div>
                        </div>

                        <div className="mt-6">
                            <button
                                onClick={() => setShowDetails(null)}
                                className="w-full py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                            >
                                {texts.close}
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
} 